import { mapState, mapMutations } from 'vuex'
import { upyunUrl } from '../../api/config'

export default {
  data () {
    return {
      upyunUrl
    }
  },
  computed: {
    ...mapState([
      'userInfo'
    ])
  },
  methods: {
    ...mapMutations({
      setUserInfo: 'SET_USERINFO'
    }),
    onSkipMeListFn (item) {
      const _this = this
      if (this.userInfo || !item.isLogin) {
        if (item.url) {
          uni.navigateTo({
            url: item.url
          })
        } else {
          uni.showModal({
            title: '提示',
            content: '是否要退出登录!',
            success () {
              uni.removeStorageSync('openid')
              _this.setUserInfo('')
            }
          })
        }
      } else {
        uni.navigateTo({
          url: `/pages/login/index`
        })
      }
    }
  },
  props: {
    lists: Array,
    default: []
  }
}
